import { Component, Host, Prop, State, h } from '@stencil/core';
import { Memoize, isEmpty, parseIfString } from '@springbokagency/stencil-base';
import { RadioButtonItem } from './form-radio-button.types';

/**
 * The form radio component.
 */
@Component({
  tag: 'bs-form-radio-button',
  styleUrl: 'form-radio-button.scss',
  shadow: true,
})
export class BsFormRadioButton {
  private formFieldRef: HTMLBsFormFieldElement;

  /**
   * State
   */

  @State() invalid: boolean = false;

  @State() focused: boolean = false;

  @State() value: string;

  /**
   * The unique name to identify the form element
   */
  @Prop() name!: string;

  /**
   * The error message to be displayed when the required field remains empty
   */
  @Prop() requiredMessage: string;

  /**
   * Wether or not the checkbox is required
   */
  @Prop() required?: boolean = true;

  /**
   * The label displayed after the checkbox
   */
  @Prop() label?: string = '';

  /**
   * A list of values for each radio button
   */
  @Prop() options: RadioButtonItem[] | string;

  @Memoize('options')
  get parsedOptions(): RadioButtonItem[] {
    return parseIfString(this.options);
  }

  /**
   * Methods
   */

  private handleOnChange = (e: PointerEvent) => {
    const el = e.target as HTMLInputElement;

    if (el.checked) {
      this.value = el.value;
      this.formFieldRef.value = this.value;
    }
  };

  componentDidLoad() {
    this.parsedOptions.forEach((item) => {
      if (item.selected) {
        this.value = item.value;
        this.formFieldRef.value = this.value;
      }
    });
  }

  render() {
    return (
      <Host>
        <fieldset class="form-fieldset">
          <legend class="form-legend">
            {this.label}
            {this.required && !isEmpty(this.label) ? <span aria-hidden="true">&nbsp;*</span> : ''}
          </legend>
          <bs-form-field
            ref={(el) => {
              this.formFieldRef = el;
            }}
            value={this.value}
            name={this.name}
            invalid={this.invalid}
            required={this.required}
            requiredMessage={this.requiredMessage}
            label={this.label}
            border={false}
          >
            {this.parsedOptions.map((radio, index) => (
              <div slot="form-element" class="form-group">
                <input
                  class="form-input"
                  id={`radio-${index}`}
                  type="radio"
                  name={this.name}
                  value={radio.value}
                  disabled={radio.disabled}
                  checked={radio.selected}
                  onChange={this.handleOnChange}
                />
                <label class="form-label" htmlFor={`radio-${index}`} title={radio.text}>
                  {radio.text}
                </label>
              </div>
            ))}
          </bs-form-field>
        </fieldset>
      </Host>
    );
  }
}
